<template>
    <div class="page">
        <div v-model="packList" style="margin-top:0.1rem;" draggable="true">
                <div v-for="item,index in packList" :key="item.id" class="packListItem">
                    <div :class="item.cho?'head active':'head'">
                        <div class="left">
                            <i :class="item.cho?'el-icon-arrow-down':'el-icon-arrow-right'" @click.stop="item.cho=!item.cho"></i>
                            <span>{{item.groupName}}</span>
                        </div>
                        <div class="right">
                            <i class='el-icon-delete' v-if="item.cho"></i>
                        </div>
                    </div>
                    <ul class="itemList" v-if="item.cho">
                        <li>
                            <img src="./img/add.png" alt="">
                        </li>
                        <li v-for="itemList in item.groBusPackagesList" @mouseover="itemList.edit=true" @mouseleave="itemList.edit=false" >
                            <img src="./img/list_temp.png" alt="">
                            <span>{{itemList.busPackageName}}</span>
                            <div class="tips" v-if="itemList.edit">
                                <div class="del">
                                    <i class='el-icon-delete'></i>
                                    <span>删除</span>
                                </div>
                                <div class="edit">
                                    <i class='el-icon-information'></i>
                                    <span>编辑</span>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>

        </div>
    </div>
</template>

<script>
    import {mapState,mapActions} from 'vuex';
    export default {
        data() {
            return {


            }
        },
        computed:{
            ...mapState(['packList']),
        },
        created(){
            this.getPackList();
        },
        methods:{
            ...mapActions(['getPackList']),
        },

    };
</script>

<style lang="scss">
    .packListItem{
        .head{
            height:0.6rem;
            line-height:0.6rem;
            background:#eee;
            display: flex;
            justify-content: space-between;
            padding:0 0.4rem 0 0.2rem;
            &.active{
                background:#ddd
            }
        }
        .el-icon-delete{
            color:red;
        }
        .itemList{
            display: flex;
            flex-wrap:wrap ;
            li{
                margin-top:0.1rem;
                list-style: none;
                width:1.8rem;
                height:1.5rem;
                display: flex;
                justify-content: center;
                flex-wrap:wrap ;
                cursor:pointer;
                position:relative;
                img{
                    margin:0 0.15rem;
                    width:1.2rem;
                    height:1.2rem;
                }
                span{
                    width:1.2rem;
                    text-align: center;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow:ellipsis;
                }
                .tips{
                    position:absolute;
                    right:-0.2rem;
                    top:-0.2rem;
                    span{
                        display: inline-block;
                        margin-top:0.02rem;
                        width:0.5rem;
                        background:yellow;
                        padding:0.01rem 0.05rem;
                        font-size:0.12rem;
                        color:#333;
                    }
                    .edit{
                        i{
                            color:green;
                        }
                    }
                }
            }
        }
    }
</style>



